<template>
  <v-container>
    <v-row>
        <v-col md="6" id="text-title"><h3>Reservas de habitaciones</h3></v-col>
      <v-col md="12">
          <v-simple-table height="300px">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">Usuario</th>
              <th class="text-left">Tipo habitación</th>
              <th class="text-left">Habitación</th>
              <th class="text-left">Servicio</th>
              <th class="text-left">Catidad Personas</th>
              <th class="text-left">Estado</th>
              <th class="text-left">Fecha de creación</th>
              <th class="text-left">Fecha inicio</th>
              <th class="text-left">Fecha fin</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, i) in reservaVisualizar" :key="i">
              <td>
                <div v-for="(itemUser, i) in usuariosVisualizar" :key="i">
                  <div v-if="item.idusuario == itemUser.idUsuario">
                    {{itemUser.nombre}}
                  </div>
                </div>
              </td>
              <td>
                <div v-for="(itemCate, i) in categoriaHabitacion" :key="i">
                  <div v-if="item.idcategoriaHab == itemCate.idcategoriaHab">
                    {{itemCate.titulo}}
                  </div>
                </div>
              </td>
              <td>
                <div v-for="(itemHab, i) in habitacionesVisualizar" :key="i">
                  <div v-if="item.idhabitacion == itemHab.idhabitacion">
                    {{itemHab.nombre}}
                  </div>
                </div>
              </td>
              <td>
                <div v-for="(itemServ, i) in Servicioshabitacion" :key="i">
                  <div v-if="item.idservicio == itemServ.idservicio">
                    {{itemServ.servicio}}
                  </div>
                </div>
              </td>
              <th>{{item.cantPersonas}}</th>
              <td>
                  <div v-if="item.estado == true">
                      Activo
                  </div>
                  <div v-if="item.estado == false">
                      Inactivo
                  </div>
              </td>
              <td>{{item.created_at.substr(2,8) }}</td>
              <td>{{item.fechaEntrada}}</td>
              <td>{{item.fechaSalida}}</td>
              
              <td>
                  <v-btn
                    class="mx-2"
                     v-if="item.estado == true"
                    @click="BorrarReservas(item.idreserva)"
                    fab
                    dark
                    small
                    color="red"
                    >
                    <v-icon dark>
                        mdi-delete
                    </v-icon>
                    </v-btn>

                    <v-btn
                    class="mx-2"
                     v-if="item.estado == false"
                    @click="RecuperarReservas(item.idreserva)"
                    fab
                    dark
                    small
                    color="yellow"
                    >
                    <v-icon dark>
                        mdi-minus-circle
                    </v-icon>
                    </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      </v-col>

      <!-- dialog de Creacion  de reservas -->

      <v-row justify="start">
    <v-dialog
      v-model="dialogCrear"
      persistent
      max-width="600px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="primary"
          dark
          v-bind="attrs"
          v-on="on"
        >
          Crear reservas
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5">Creación de reservas</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form
              v-model="validacionCreacion"
            >
            <v-row
            no-gutters
            >
              <v-col
                cols="12"
                sm="6"
              >
                <v-select
                  v-model="reservar.idusuario"
                  :items="usuariosVisualizar"
                  item-text="nombre"
                  item-value="idUsuario"
                  label="Usuario"
                  :rules="Rules"
                  required
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-select
                  v-model="reservar.idcategoriaHab"
                  :items="categoriaHabitacion"
                  item-text="titulo"
                  item-value="idcategoriaHab"
                  label="Categoria de habitación"
                  :rules="Rules"
                  required
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-select
                  v-model="reservar.idhabitacion"
                  :items="habitacionesVisualizar"
                  item-text="nombre"
                  item-value="idhabitacion"
                  label="Habitación"
                  :rules="Rules"
                  required
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-select
                  v-model="reservar.idservicio"
                  :items="Servicioshabitacion"
                  item-text="servicio"
                  item-value="idservicio"
                  label="Servicio"
                  :rules="Rules"
                  required
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-menu
                  ref="menu"
                  v-model="menu"
                  :close-on-content-click="false"
                  :return-value.sync="date"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="reservar.fechaEntrada"
                      :rules="Rules"
                      label="Fecha de entrada"
                      required
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="reservar.fechaEntrada"
                    no-title
                    scrollable
                  >
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="menu = false">
                      Cancel
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="$refs.menu.save(date)"
                    >
                      OK
                    </v-btn>
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-menu
                  ref="menu"
                  v-model="menu2"
                  :close-on-content-click="false"
                  :return-value.sync="date"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="reservar.fechaSalida"
                      :rules="Rules"
                      label="Fecha de salida"
                      required
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="reservar.fechaSalida"
                    no-title
                    scrollable
                  >
                    <v-spacer></v-spacer>
                    <v-btn text color="primary" @click="menu = false">
                      Cancel
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="$refs.menu.save(date)"
                    >
                      OK
                    </v-btn>
                  </v-date-picker>
                </v-menu>
              </v-col>
              <v-col 
                cols="12"
                sm="6"
              >
                <v-text-field
                  v-model="reservar.cantPersonas"
                  type="number"
                  label="Cantidad de personas"
                  :rules="Rules"
                  required
                ></v-text-field>
              </v-col>

              <v-col md="3" style="margin-right: 390px">
                  <v-btn
                      @click="enviarReservas()"
                      :disabled="!validacionCreacion"
                      class="mr-4"
                      color="success"
                  >
                  Enviar
                  </v-btn>
              </v-col>
              
            </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialogCrear = false"
          >
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
  
    <!-- Fin del dialog -->
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
      reservaVisualizar: [],
      habitacionesVisualizar: [],
      categoriaHabitacion: [],
      Servicioshabitacion: [],
      usuariosVisualizar: [],
      dialogCrear: false,
      validacionCreacion  : true,
      menu: false,
      menu2: false,
      Rules: [
        v => !!v || 'El campo es requerido',
      ],

      reservar: {
        fechaEntrada: "",
        fechaSalida: "",
        cantPersonas: 0,
        estado: true,
        idservicio: 0,
        idhabitacion: 0,
        idcategoriaHab: 0,
        idusuario: 0,
      },
  }),

  created(){
      this.obtenerReservas();
      
  },

  methods:{

    BorrarReservas: async function (id) {
          let Aidi = parseInt(id)
          let estado = {estado: false};
        console.log("Hola");
        await axios.put("http://localhost:3000/reserva/cambioEstado/"+Aidi, estado).then((resp) => {
            if (resp.status == 204) {
                this.obtenerReservas();
            }
        });
        console.log(this.ObtenerEntidades);
      },
      RecuperarReservas: async function (id) {
          let Aidi = parseInt(id)
          let estado = {estado: true};
        console.log("Hola");
        await axios.put("http://localhost:3000/reserva/cambioEstado/"+Aidi, estado).then((resp) => {
            if (resp.status == 204) {
                this.obtenerReservas();
            }
        });
        console.log(this.ObtenerEntidades);
      },

      enviarReservas: async function () {
        if (this.reservar.fechaEntrada == "") {
          alert("Complete Las fechas de la reserva.");
          return 0;
        }
        // if (this.fechaSalida == "") {
        //   alert("Complete Las fechas de la reserva.");
        //   return 0;
        // }
        if (this.reservar.idusuario == "") {
          alert("Dato de reserva añadidio correctamente.");
          return 0;
        }
        this.reservar.cantPersonas = parseInt(this.reservar.cantPersonas)
        console.log("Hola");
        await axios.post("http://localhost:3000/reserva/create", this.reservar).then((resp) => {
            if (resp.status == 201) {
            alert("Reserva hecha correctamente.");
            location.reload();
            }
        });
        console.log(this.reservar);
      },
      obtenerReservas: async function () {
        console.log("Hola");
        await axios.get("http://localhost:3000/reserva").then((resp) => {
            if (resp.status == 200) {
            this.reservaVisualizar = resp.data;
            this.obtenerUsuarios();
            this.obtenerTipoHabitaciones();
            this.obtenerHabitaciones();
            this.obtenerServicios();
            }
        });
        console.log(this.reservaVisualizar);
      },

      obtenerUsuarios: async function () {
        console.log("Hola");
        await axios.get("http://localhost:3000/usuario").then((resp) => {
            if (resp.status == 200) {
            this.usuariosVisualizar = resp.data;
            }
        });
      },

      obtenerTipoHabitaciones: async function () {
        console.log("Hola");
        await axios.get("http://localhost:3000/categoriaHab").then((resp) => {
            if (resp.status == 200) {
            this.categoriaHabitacion = resp.data;
            }
        });
        console.log(this.categoriaHabitacion);
      },

      obtenerHabitaciones: async function () {
        console.log("Hola");
        await axios.get("http://localhost:3000/habitaciones/obtener").then((resp) => {
            if (resp.status == 200) {
            this.habitacionesVisualizar = resp.data;
            }
        });
        console.log(this.habitacionesVisualizar);
      },

      obtenerServicios: async function () {
        console.log("Hola");
        await axios.get("http://localhost:3000/servicios").then((resp) => {
            if (resp.status == 200) {
            this.Servicioshabitacion = resp.data;
            }
        });
        console.log(this.Servicioshabitacion);
      },
  }
};
</script>


<style scoped>
    #text-title{
        margin-top: 20px;
        margin-left: 40%;
    }
</style>